import React from "react";
import {connect} from "react-redux";
import {Col, Popover, Radio, Row} from 'antd';
import _ from 'lodash';
import {setPlayerValue} from "../../store/player-actions";

const RadioButton = Radio.Button;
const RadioGroup = Radio.Group;

class UIAnimalBar extends React.Component {

  onChange(e) {
    const id = e.target.value;
    setPlayerValue({
      fightN: true,
      fightA: id,
    });
  }

  info(id) {
    const {animal} = this.props;
    const ani = animal[id];
    return (
      <div>
        <Row><Col span={8}></Col><Col span={16}>{ani[1]}-{ani[9]}</Col></Row>
        <Row><Col span={8}></Col><Col span={16}>{ani[3]}</Col></Row>
        <Row><Col span={8}></Col><Col span={16}>{ani[4]}</Col></Row>
        <Row><Col span={8}></Col><Col span={16}>{ani[2]}</Col></Row>
        <Row><Col span={8}></Col><Col span={16}>{ani[5]}</Col></Row>
        <Row><Col span={8}></Col><Col span={16}>{ani[6]}</Col></Row>
        <Row><Col span={8}></Col><Col span={16}>{ani[7]}{ani[8]}</Col></Row>
      </div>);
  }

  render() {
    const {maping, map, animal} = this.props;
    if (map[maping][3].length === 0)
      return null;
    const ids = _(map[maping][3]).map((id) => {
      const ani = animal[id];
      return (
        <Popover key={`animal_${id}`} content={this.info(id)} title={` - <${ani[0]}>`}>
          <RadioButton value={id}>{ani[0]}</RadioButton>
        </Popover>
      )
    }).value();
    return (
      <div>
        <Row>
          <Col span={4}>
            
          </Col>
          <Col>
            <RadioGroup onChange={this.onChange.bind(this)}>
              {ids}
            </RadioGroup>
          </Col>
        </Row>
      </div>
    );
  }
}

export default connect((state, props) => {
  return {
    maping: state.player.maping,
    map: state.settings.map,
    animal: state.settings.animal,
  };
})(UIAnimalBar);
